.message {
    display: flex;
    align-items: flex-end;
    margin-bottom: 15px;
}

.avatarImg {
    width: 40px;
    height: 45px;
}

.messageBlock {
    display: flex;
    min-width: 100px;
    position: relative;
    justify-content: space-between;
    flex-direction: column;
    padding: 5px;
    background-color: rgba(130, 162, 130, 0.6);
    border-radius: 5px;
    margin-left: 10px;
}

.messageBlock::before {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    border-left: 7px solid transparent;
    border-right: 7px solid rgb(130, 162, 130, 0.6);
    border-top: 7px solid transparent;
    border-bottom: 7px solid rgb(130, 162, 130, 0.6);
    left: -14px;
    bottom: 5px;
}

.userName {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 5px;
    color: rgb(243, 246, 252);
}

.userMessage {
    text-align: center;
    font-size: 16px;
    color: rgb(255, 255, 255);
    margin: 0 15px;
}

.messageTime {
    font-size: 14px;
    text-align: end;
    color: rgb(104, 108, 104);
}

